.toggle {
position: relative;
display: inline-block;
width: 40px;
height: 20px;
margin: 0;
margin-right: 10px;
vertical-align: text-top;
background: #fff;
border: 1px solid $Purple;
border-radius: 10px;
box-sizing: border-box;
outline: none;
cursor: pointer;
-webkit-appearance: none;
-moz-appearance: none;
appearance: none;
}

.toggle:after {
content: "";
display: inline-block;
position: absolute;
left: 4px;
top: 3px;
width: 12px;
height: 12px;
background: $DarkShade25;
border-radius: 50%;
transform: translateX(0);
}

.toggle:checked::after {
transform: translateX(calc(100% + 6px));
background: linear-gradient(180deg, $Purple 0%, #8BA4F9 100%);
}

